code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100%;
}

body {
  margin: 0;
  min-height: 100%;
  padding-bottom: 30px;
  position: relative;
  background-image: linear-gradient(to bottom, transparent, black),
    url("../src/assets/images/bkg.jpg");
  /* Photo by Toa Heftiba on Unsplash*/
  background-repeat: repeat-x;
  background-attachment: fixed;
  background-color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
}

.container {
  margin-top: 10px;
  margin-bottom: 50px;
}

.jumbotron {
  opacity: 0.7;
  color: white;
}

.card {
  border: 0;
  background-color: rgba(0, 0, 0, 0.5);
  filter: grayscale(100%);
}

.card:hover {
  filter: grayscale(0%);
}
.card:hover > img {
  filter: opacity(50%) blur(3px);
}

.card-img {
  filter: opacity(80%);
}

.card-img-overlay > h5,
.card-img-overlay > p {
  visibility: hidden;
}
.card-img-overlay:hover h5,
.card-img-overlay:hover p {
  visibility: visible;
}

.card-text,
.card-title,
.card-footer {
  text-shadow: 1px 1px black;
}

footer {
  clear: both;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

#about {
  /* width: calc(100% -10rem); */
  margin-left: 10rem;
  margin-right: 10px;
}

.vertical-nav {
  position: fixed;
  top: 20%;
  width: 9rem;
  /* border-radius: 10px;
  border-style: dashed;
  border-color: aliceblue; */
}

table {
  color: rgba(255, 255, 255, 0.575) !important;
}

a {
  color: white !important;
}

.entrance {
  margin-top: 100px;
  font-family: "Bitter", serif;
  font-size: 100px;
  padding: 10px;
  display: inline-block;
  overflow: hidden;
}
